export default {
	props: {
		// 左侧图标名称(只能uView内置图标)，或者图标src
		icon: {
			type: String,
			default: ''
		},
		// 左侧标题
		title: {
			type: String,
			default: ''
		},
		// 右侧内容
		value: {
			type: String,
			default: ''
		},
		// 标题下方的描述信息
		label: {
			type: String,
			default: ''
		},
		// 是否显示内边框
		border: {
			type: Boolean,
			default: true
		},
		// 是否开启点击反馈，即点击是cell背景为灰色
		hover: {
			type: Boolean,
			default: true
		},
		// 是否显示右侧箭头
		arrow: {
			type: Boolean,
			default: true
		},
		// 右侧箭头方向，可选值：right|up|down，默认为right
		arrowDirection: {
			type: String,
			default: 'right'
		},
		// 控制标题的样式
		titleStyle: {
			type: Object,
			default() {
				return {};
			}
		},
		// 右侧显示内容的样式
		valueStyle: {
			type: Object,
			default() {
				return {};
			}
		},
		// 描述信息的样式
		labelStyle: {
			type: Object,
			default() {
				return {};
			}
		},
		// 背景颜色
		bgColor: {
			type: String,
			default: '#ffffff'
		},
		// 用于识别被点击的是第几个cell
		index: {
			type: [String, Number],
			default: ''
		}
	},
	inject: ['uCellGroup'],
	data() {
		return {
			itemIndex: 0,
		}
	},
	created() {
		this.itemIndex = this.uCellGroup.index++;
	},
	computed: {
		arrowStyle() {
			let style = {};
			if(this.arrowDirection == 'top') style.transform = 'rotate(-90deg)';
			else if(this.arrowDirection == 'bottom') style.transform = 'rotate(90deg)';
			else style.transform = 'rotate(0deg)';
			return style;
		}
	},
	methods: {
		click() {
			this.$emit('click', this.index);
		}
	}
}
